.countryInfor {
    margin-left: 20px;
}

.countryName {
    font-size: 20px;
    font-weight: bold;
    padding: 8px;
    margin-bottom: 8px;
}

table {
    border-collapse: collapse;
    width: 100%;
    font-size: 18px;
}

td, th {
    text-align: left;
    padding: 8px;
}

.countryInfor__title {
    font-weight: 600;
    max-width: max-content;
}

.countryInfor__value {
    font-weight: 400;
    margin-left: 4px;
}

.borderList {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.borderItem {
    display: block;
    padding: 4px 8px;
    margin: 8px 12px 8px 0;
    width: max-content;
    text-align: center;
    user-select: none;
    transition: all 0.2s ease;
    opacity: 0.75;
    font-size: 14px;
    font-weight: 700;
    border-radius: 4px;
    &:hover {
        opacity: 1;
        font-weight: bold;
        cursor: pointer;
    }
}